<template>
  <div id="article">
    <div class="container">
      <div class="left">
        <div class="articleDetailPage">
          <div class="title">
            {{article.title}}
          </div>
          <div class="name-time">
            <div class="name">
              <img src="@/assets/icons/user.png">
              <span>淡然</span>
            </div>
            <div class="time">
              <img src="@/assets/icons/time.png">
              <span>{{article.create_time}}</span>
            </div>
          </div>
          <div class="body">
            <li class="p-img" v-for="item in article.body">
              <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{item.para}}</p>
              <img :src="item.image">
            </li>
          </div>
        </div>
      </div>
      <div class="right">
        <div class="attentMe">
          <attent-me/>
        </div>
        <div class="newArticles">
          <new-articles 
          :arrayList="articleList.slice(0,6)"
          @refresh="reload"/>
        </div>
        <div class="links">
          <links/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import AttentMe from '@/components/public/attentMe'
import NewArticles from '@/components/public/newArticles'
import Links from '@/components/public/links'
import { getArticleByTitle, getArticlesTypeInTS } from '@/api/getData'

export default {
  name: 'article',
  data(){
    return{
      prop: {
        title: ''
      },
      article: {},
      articleList: []
    }
  },
  components: {
    AttentMe,
    NewArticles,
    Links
  },
  methods: {
    reload(data){
      let _this = this
      _this.prop.title = this.$route.query.title
      getArticleByTitle(_this.prop)
      .then(res => {
        console.log(res)
        _this.article = res.data.article
      })
      document.body.scrollTop = 0
      document.documentElement.scrollTop = 0
    }
  },
  beforeMount(){
    let _this = this
    /*获取文章详情*/
    _this.prop.title = this.$route.query.title
    console.log(_this.prop.title)
    getArticleByTitle(_this.prop)
    .then(res => {
      console.log(res)
      _this.article = res.data.article
    })
    /*获取所有“技术分享”和“韶华追忆”文章*/
    getArticlesTypeInTS()
    .then(res => {
      console.log(res)
      _this.articleList = res.data.articles.reverse()
    })
  }
};
</script>

<style lang="scss">
  @import "../../assets/scss/article/index.scss";
